'use strict';

import React, {Component} from 'react';
import ReactDOM from 'react-dom';
import View from './../../components/View/View.web';
import TextInputState from './../../components/TextInput/TextInputState.web';

class AppContainer extends Component {
	constructor(props){
		super(props);
	}
	//关于软键盘弹起时会将页面顶出可视区域的问题，已经在TextInput中修复。
	/*
	_resizeHandler(){
	    //tp('_resizeHandler');
        setTimeout(function(){
            var node = TextInputState.currentFocus();
            if(node){
                global.currentApp.isCheckKeyborad=true;
                node.scrollIntoViewIfNeeded();
            }
        },600);

		this.forceUpdate();
	}

	componentDidMount(){
     global.addEventListener('resize', ()=>{
			this._resizeHandler();
		}, false);
	}

	componentWillUnmount(){
     global.removeEventListener('resize', ()=>{
			this._resizeHandler();
		}, false);
	}
	*/

    render() {
        let RootComponent = this.props.rootComponent;
        let appView =
            <View ref="main" >
                <RootComponent
                    {...this.props.initialProps}
                    rootTag={this.props.rootTag}/>
            </View>;

        return appView;
    }
}

function renderApplication(
    RootComponent,
    initialProps,
    rootTag
) {

    ReactDOM.render(
        <AppContainer
            rootComponent={RootComponent}
            initialProps={initialProps}
            rootTag={rootTag} />,
        rootTag
    );
}
